import { Droppable } from "react-beautiful-dnd";
import { Title, DropArea, BoardContainer, Form } from "../style/DndStyle";
import DraggableCard from "./DraggableCard";
import { useForm } from "react-hook-form";
import { ITodo, toDoState } from "../atom/atoms";
import { useSetRecoilState } from "recoil";

interface ITodoProps {
  toDos: ITodo[];
  boardId: string;
}

export interface IDropArea {
  isDraggingOver: boolean;
  draggingFromThisWith: boolean;
}

interface IForm {
  toDo: string;
}

const Board = ({ toDos, boardId }: ITodoProps) => {
  // useRef => Dom 
  const { register, handleSubmit, setValue } = useForm<IForm>();
  const setTodo = useSetRecoilState(toDoState);

  const handleValid = ({ toDo }: IForm) => {
    const newItem = {
      id: Date.now(),
      text: toDo,
    };
    setTodo((allBoards) => {
      return {
        ...allBoards,
        [boardId]: [...allBoards[boardId], newItem],
      };
    });
    setValue("toDo", "");
  };

  const removeBoard = () => {
    setTodo((allBoards) => {
      const copyBoards = { ...allBoards };
      delete copyBoards[boardId];

      return {
        ...copyBoards,
      };
    });
  };

  return (
    <Form onSubmit={handleSubmit(handleValid)}>
      <BoardContainer>
        <img src="./img/close.png" alt="  " onClick={removeBoard} style={{ cursor: "pointer" }} />
        <Title>{boardId}</Title>
        <input type="text" {...register("toDo", { required: " " })} placeholder=" " />
        <button>click</button>
        <Droppable droppableId={boardId}>
          {/* snapshot Droppable  arg (    go to type definition) */}
          {(provided, snapshot) => (
            <DropArea
              draggingFromThisWith={Boolean(snapshot.draggingFromThisWith)}
              isDraggingOver={snapshot.isDraggingOver}
              ref={provided.innerRef}
              {...provided.droppableProps}
            >
              {toDos.map((toDo, index) => {
                return (
                  // Draggable key draggableId  
                  <DraggableCard boardId={boardId} key={toDo.id.toString()} toDoId={toDo.id} toDoText={toDo.text} index={index} />
                );
              })}
              {/* beautiful dnd param    (   X) */}
              {provided.placeholder}
            </DropArea>
          )}
        </Droppable>
      </BoardContainer>
    </Form>
  );
};

export default Board;
